html,
body {
  padding: 0;
  margin: 0;
  max-width: 100%;
  height: auto !important;
}
.swiper-button-prev,
.swiper-button-next {
  margin-top: -100px !important;
}

@-ms-viewport {
  width: device-width !important;
}

.swiper-container {
  width: 700px;
  height: 600px;
  margin-top: 7em;
  text-align: center;
  overflow: hidden;
}
.swiper-slide {
  text-align: center;
}

.swiper-pagination {
  bottom: 3em !important;
}

#intro-child {
  display: flex;
  justify-content: center;
}
a {
  color: inherit;
  text-decoration: none;
}
html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}
* {
  margin: 0;
  padding: 0;
}
.burger-btn {
  width: 28px;
  height: 3px;
  background-color: white;
  margin: 6px 0;
}
#burger-parent {
  position: absolute;
  justify-content: center;
  cursor: pointer;
  bottom: 16px;
  left: 90%;
  display: none;
}

#navbar {
  background-color: #3f3c3c;
  position: sticky;
  margin-top: -10px;
  padding: 12px;
  border-bottom: 3px solid #8edbee;
  top: 0;
  z-index: 999;
  font-family: "Montserrat Black";
  text-transform: uppercase;
  font-size: 17px;
  display: block;
  transition: 1s all ease-out;
}

#navbar li {
  display: inline;
  list-style-type: none;
  color: white;
  transition: all 0.4s ease;
  cursor: pointer;
  margin: 15px;
}
#navbar li:hover {
  color: rgb(228, 88, 88);
}
.check-work-btn {
  border: 2px solid #fff;
  text-decoration: none;
  color: #fff;
  padding: 20px;
  font-size: 1.3em;
  margin-top: 1em;
  font-family: "Montserrat Black";
  text-transform: uppercase;
  position: relative;
  height: 15px;
  line-height: 15px;
  transition: all 0.2s ease-in-out;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

.check-work-btn:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: white;
  transition: 0.3s;
  z-index: -3;
}
.check-work-btn:hover:before {
  width: 100%;
}
.check-work-btn:hover {
  transition: all 0.3s;
  color: black;
  font-size: 1.3em;
}

#check-work-parent {
  display: flex;
  justify-content: center;
}
